/**
 * Site header
 */
.site-header {
  min-height: 56px;

  // Positioning context for the mobile navigation icon
  position: relative;
}

.site-title {
  font-size: 28px;
  font-weight: 400;
  line-height: 56px;
  letter-spacing: 0px;
  margin-bottom: 0;
  margin-left: 0px;
  float: left;

  &,
  &:visited {
    color: $grey-color-dark;
  }
}

.site-nav {
  float: right;
  line-height: 56px;

  .page-link {
    color: $text-color;
    font-size: 24px;
    line-height: $base-line-height;

    // Gaps between nav items, but not on the last one
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  @include media-query($on-palm) {
    .page-link {
      padding: 20px 0;

      margin-left: 20px;
      &:not(:last-child) {
        margin-right: 0;
      }
    }
  }
}



/**
 * Site footer
 */
.site-footer {
  padding: 0px;
  font-size: 18px;
  color: $grey-color;
  text-align: center;
  padding-left: 0px;
  padding-top: 0px;
  margin-left: 0px;
  padding-left: 0px;
  margin-bottom: 0px;
  padding-bottom: 0px;
}



/**
 * Page content
 */
.page-content {
  padding: $spacing-unit 0;
  margin-bottom: 0px;
}

.page-heading {
  font-size: 20px;
}

.post-list {
  margin-left: 0;
  list-style: none;

  .post-link:hover {
    text-decoration: none;
  }

  > li {
    // margin-bottom: $spacing-unit * 4;
    margin-bottom: 10px;

    &:not(:first-child) {
      border-top: 4px solid $grey-color-light;
      padding-top: $spacing-unit * 2;
    }
  }
}

.post-archives {
  margin-left: 0;
  list-style: none;

  .post-link {
    font-size: 24px;
  }

  > li {
    margin-bottom: $spacing-unit;
  }
}

.post-meta {
  font-size: $small-font-size;
  color: $grey-color;
}

.post-link {
  &,
  &:visited {
    color: $text-color;
  }

  &:hover {
    color: $brand-color;
  }
}

.post-continue {
  a {
    padding: 8px 15px;
    text-decoration: none;

    &,
    &:visited {
      color: $grey-color-dark;
      background-color: $grey-color-light;
    }

    &:hover {
      color: white;
      background-color: $brand-color;
    }
  }
}



/**
 * Posts
 */
.post-header {
  margin-bottom: $spacing-unit;
}

.post-title {
  font-size: 24px;
  letter-spacing: -1px;
  margin-left: 0px;
  line-height: 1;

  @include media-query($on-laptop) {
    font-size: 36px;
  }
}

.post-content {
  margin-bottom: $spacing-unit;

  li {
    margin-bottom: $spacing-unit * 0.1;

    :first-child {
      margin-bottom: $spacing-unit * 0.01;
    }
  }

  h1 {
    font-size: 38px;

    @include media-query($on-laptop) {
      font-size: 34px;
    }
  }

  h2 {
    font-size: 26px;

    @include media-query($on-laptop) {
      font-size: 24px;
    }
  }

  h3 {
    font-size: 26px;

    @include media-query($on-laptop) {
      font-size: 22px;
    }
  }

  h4 {
    font-size: 20px;

    @include media-query($on-laptop) {
      font-size: 18px;
    }
  }
}


.news {
  padding: 1.5rem 0;
  margin-bottom: 0rem;
}

.news h3 {
  font-size: 1.8rem;
  padding-bottom: 0.0rem;
  padding-top: 0px;
  margin-bottom: 2px;
  margin-top: 0px;
}

.news-container {
  max-height: 300px; /* Adjust height based on preference */
  border: 2px solid #bbb; /* Light border around the container */
  padding: 0px; /* Add some padding inside the container */
  padding-left: 5; /* Remove default padding */
  overflow-y: auto; /* Make the scrollbar visible */
  font-size: 16px; /* Set font size for the container */
}

.news ul {
  list-style-type: none; /* Remove bullet points */
  padding-left: 5;
  padding-right: 10px;
  margin: 2px;
  margin-bottom: 0px;
}

.news ul li {
  margin-bottom: 0.5rem; /* Increase space between items (was 1.2rem) */
  line-height: 1.6; /* Space between lines */
}

.news ul li .news-date {
  display: inline-block; /* Keep date and content inline */
  font-weight: bold;
  font-size: 16px; /* Set font size for the date */
  color: #000; /* Black color for the date */
  margin-right: 2px;
  margin-left: 5px;
}

.news ul li .news-content {
  // display: inline-block; /* Make sure content stays on the same line */
  font-size: 16px; /* Set font size for content */
  color: #333; /* Standard text color */
  margin-right: 5px;
  padding-right: 5px;
}

.news ul li .news-content a {
  color: #007acc; /* Customize link color */
  text-decoration: none;
  margin-left: auto;
  margin-right: auto;
  padding-right: 0px;
}

.news ul li .news-content a:hover {
  text-decoration: underline;
  margin-right: auto;
  padding-right: 0px;
}

// Profile
.profile {
  img {
    width: 100%;
  }
}


/**
 * Pagination
 */
.pagination {
  padding: $spacing-unit * 0.5 0;
  border-top: 1px solid $grey-color-light;
  border-bottom: 1px solid $grey-color-light;
  text-align: center;
  @extend %clearfix;

  a {
    &,
    &:visited {
      color: $grey-color;
    }

    &:hover {
      color: $brand-color;
    }
  }

  .previous {
    float: left;
  }

  .next {
    float: right;
  }
}

.custom-width {
  flex: 0 0 18.66%; /* 2 columns = 16.66% * 2 = 33.33%, so choose a value between 16.66% and 33.33% */
  max-width: 18.66%; /* Adjust this value based on your needs, making sure it's between 1 and 2 column widths */
}

