@import "fonts";

// Define defaults for each variable.
$base-font-family: "Noto Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
// Bitter, "Apple SD Gothic Neo", "Noto Sans", "Source Han Sans", "Noto Sans CJK JP", "Source Han Sans JP", "Noto Sans CJK KR", "Source Han Sans KR", NanumBarunGothic, AppleGothic, "Malgun Gothic", Dotum, sans-serif !default;
$monospace-font-family: "Noto Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
// Monaco, Menlo, Consolas, "Courier New", DotumChe, monospace !default;
$base-font-size:   18px !default;
$base-font-weight: 400 !default;
$small-font-size:  $base-font-size * 0.875 !default;
$base-line-height: 1.5 !default;

$spacing-unit:     30px !default;

$grey-color:       #757575 !default;
$grey-color-light: lighten($grey-color, 45%) !default;
$grey-color-dark:  darken($grey-color, 60%) !default;

// $text-color:       #111 !default;
$text-color:       $grey-color-dark !default;
$background-color: #fdfdfd !default;
$brand-color:      #2568ba !default;

// Width of the content area
$content-width:    1320px !default;

$on-palm:          600px !default;
$on-laptop:        1320px !default;



// Use media queries like this:
// @include media-query($on-palm) {
//   .wrapper {
//     padding-right: $spacing-unit * 0.5;
//     padding-left: $spacing-unit * 0.5;
//   }
// }
@mixin media-query($device) {
  @media screen and (max-width: $device) {
    @content;
  }
}



// Import partials.
@import
  "whiteglass/base",
  "whiteglass/layout",
  "whiteglass/syntax-highlighting"
;
