@charset "utf-8";

// Our variables
$base-font-family: "Noto Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
// Bitter, "Apple SD Gothic Neo", AppleGothic, NanumBarunGothic, "Malgun Gothic", Dotum, sans-serif;
$monospace-font-family: "Noto Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
// Monaco, Menlo, Consolas, "Courier New", DotumChe, monospace;
$base-font-size:   18px;
$base-font-weight: 400;
$small-font-size:  $base-font-size * 0.875;
$base-line-height: 1.5;

$spacing-unit:     30px;

$grey-color:       #757575;
$grey-color-light: lighten($grey-color, 45%);
$grey-color-dark:  darken($grey-color, 20%);

// $text-color:       #111;
$text-color:       darken($grey-color, 20%);
$background-color: #fdfdfd;
$brand-color:      #2568ba;


// Width of the content area
$content-width:    1150px;

$on-palm:          700px;
$on-laptop:        1100px;



// whiteglass also includes a mixin for defining media queries.
// Use media queries like this:
// @include media-query($on-palm) {
//     .wrapper {
//         padding-right: $spacing-unit * 0.5;
//         padding-left: $spacing-unit * 0.5;
//     }
// }
@mixin media-query($device) {
  @media screen and (max-width: $device) {
    @content;
  }
}



// Import partials from the `whiteglass` theme.
@import "whiteglass";
