/**
 * Reset some basic elements
 */
body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
  margin: 0;
  padding: 0;
}




/**
 * Basic styling
 */
body {
  font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
  color: $text-color;
  background-color: $background-color;
  -webkit-text-size-adjust: 100%;
  -webkit-font-feature-settings: "kern" 1;
     -moz-font-feature-settings: "kern" 1;
       -o-font-feature-settings: "kern" 1;
          font-feature-settings: "kern" 1;
  font-kerning: normal;
  word-wrap: break-word;
}

table {
  width: 100%;
  margin: 0 0 20px 0;
  text-align: left;
}

/**
 * Set `margin-bottom` to maintain vertical rhythm
 */
h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
ul, ol, dl, figure,
%vertical-rhythm {
  margin-bottom: $spacing-unit * 0.5;
}



/**
 * Images
 */
img {
  max-width: 100%;
  vertical-align: middle;
  margin-bottom: 0px;
  padding-bottom: 0px;
}



/**
 * Figures
 */
figure > img {
  display: block;
  margin-bottom: 0px;
  padding-bottom: 0px;
}

figcaption {
  font-size: $small-font-size;
  margin-bottom: 0px;
}



/**
 * Lists
 */
ul, ol {
  margin-left: $spacing-unit;
}

li {
  > ul,
  > ol {
    margin-bottom: 0;
  }
}



/**
 * Headings
 */
h1, h2, h3, h4, h5, h6 {
  font-weight: $base-font-weight;

  code {
    font-size: inherit;
  }
}

h1, h2, h3 {
  &:lang(ko) {
    word-break: keep-all;
  }
}



/**
 * Links
 */
a {
  color: $brand-color;
  text-decoration: none;
  padding-left: 0px;  
  padding-right: 0px;
  margin-left: 0px;
  margin-right: 0px;

  &:visited {
    color: darken($brand-color, 10%);
  }

  &:hover {
    color: $text-color;
    text-decoration: underline;
  }
}



/**
 * Blockquotes
 */
blockquote {
  color: $grey-color;
  border-left: 4px solid $grey-color-light;
  padding-left: $spacing-unit * 0.5;
  font-size: 18px;
  letter-spacing: -1px;
  font-style: italic;

  > :last-child {
    margin-bottom: 0;
  }

  pre, code {
    color: $text-color;
  }
}



/**
 * Rules
 */
hr {
  height: 2px;
  margin: $spacing-unit * 0.1 0;
  border: 0;
  background-color: $grey-color-light;
}



/**
 * Code formatting
 */
pre,
code {
  font-family: $monospace-font-family;
  font-size: 13px;
  border: 1px solid $grey-color-light;
  border-radius: 0;
  background-color: #f8f8f8;
}

code {
  padding: 1px 5px;
}

pre {
  padding: 8px 12px;
  overflow-x: auto;
  word-wrap: normal;

  > code {
    border: 0;
    padding-right: 0;
    padding-left: 0;
  }
}



/**
 * Wrapper
 */
.wrapper {
  max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit} * 2));
  max-width:         calc(#{$content-width} - (#{$spacing-unit} * 2));
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 0px;
  padding-bottom: 0px;
  margin-top: 0px;
  padding-top: 0px;
  padding-right: $spacing-unit;
  padding-left: $spacing-unit;
  text-align: justify;
  hyphens: auto;
  @extend %clearfix;

  @include media-query($on-laptop) {
    max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit}));
    max-width:         calc(#{$content-width} - (#{$spacing-unit}));
    padding-right: $spacing-unit * 0.5;
    padding-left: $spacing-unit * 0.5;
  }
}

.publication-wrapper {
  max-width: $content-width;
  max-width: $content-width;
  padding-right: 0;
  padding-left: 0;
  margin-bottom: 0px;
  padding-bottom: 0px;
  margin-top: 0px;
  padding-top: 0px;
  text-align: justify;
  hyphens: auto;
  @extend %clearfix;

  @include media-query($on-laptop) {
    max-width: $content-width;
    max-width: $content-width;
    padding-right: 0;
    padding-left: 0;
  }
}



/**
 * Clearfix
 */
%clearfix:after {
  content: "";
  display: table;
  clear: both;
}

.author {
  font-size: 18px;
  color: #333;
  margin-top: 10px;
  margin-bottom: 5px;
  margin-left: 5px;

  a {
    color: #007bff;
    text-decoration: none;
    margin-left: 5px;

    &:hover {
      text-decoration: underline;
    }
  }

  b {
    font-weight: bold;
  }

  .more-authors {
    cursor: pointer;
    color: #007bff;
    text-decoration: underline;
  }
}

.title {
  font-size: 18px;
  font-style: italic;
  margin-left: 5px;
}

.journal{
  font-size: 18px;
  margin-left: 5px;
}

.year{
  font-size: 18px;
  margin-left: 5px;
}

.booktitle{
  font-size: 18px;
  margin-left: 5px;
}

.abbr {
  font-size: 18px;
  color : #a00;
  margin-left: 5px;
}

.note {
  font-size: 18px;
  color : #458;
  margin-left: 5px;
}

.pdf {
  font-size: 18px;
  color : #008522;
  margin-left: 5px;
}

.rate,
.award {
  font-size: 18px;
  margin-left: 5px;
  font-weight: bold;
}

a {
  margin-left: 10px;
  color: #007bff;

  &:hover {
    text-decoration: underline;
  }
}

// Social (bottom)

.social {
  text-align: center;
  margin-bottom: 0px;
  padding-bottom: 0px;
  padding-top: 0px;
  margin-top: 0px;
  padding-left: 0px;
  padding-right: 8px;
  margin-left: 0px;
  // background-color: $grey-color-light;
  border: 2px solid #bbb;
  .contact-icons {
    margin-top: 0;
    padding-left: 0px;
    padding-right: 0px;
    margin-left: 0px;
    margin-right: 0px;
    font-size: 2.2rem;
    text-justify: center;
    margin-bottom: 0;
    padding-bottom: 0;
  }
  .contact-note {
    font-size: 0.8rem;
  }
}


// Profile

.profile {
  width: 100%;
  margin-bottom: 0px;
  padding-bottom: 0px;

  .address {
    font-size: 1rem;
    margin-bottom: 2px;
    margin-top: 0px;
    padding-top: 0px;
    font-family: "Overlock", sans-serif;
    p {
      display: inline-block;
      margin: 0;
      padding: 0;
    }
  }
}
.profile.float-right{
  float: right;
  margin-left: 1.2rem;
  justify-items: right;
  padding-bottom: 0px;
  margin-bottom: 0px;
}
.profile.float-left{
  float: left;
  margin-right: 1.2rem;
}

@media (min-width: 576px) {
  .profile {
    margin-top: 8px;
    margin-bottom: 0px;
    padding-bottom: 0px;
    width: 24%;
    .address {
      margin-top: 0px;
      padding-top: 0px;
      padding-right: 5px;
      padding-left: 5px;
      display: block; 
    }
  }
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

